@import "inc/variables.scss";

body
{
   font-family: 'Fira Sans', sans-serif;
   font-size: 10pt;
   line-height: #{(9 / 5)};
   margin: 0;
   box-sizing: border-box;
}

html[lang='zh-Hant'] > body
{
    font-family: Fira Sans, 'PingFang TC','PingFang HK','Noto Sans TC','Source Han Sans TW','Microsoft JhengHei UI','Microsoft JhengHei','微軟正黑體', sans-serif;
}

html[lang='zh-Hans'] > body
{
    font-family: Fira Sans, 'PingFang SC','Noto Sans SC','Source Han Sans CN','Microsoft YaHei UI','Microsoft YaHei','微软雅黑', sans-serif;
}

html[lang^='ko'] > body
{
    font-family: Fira Sans, 'Noto Sans KR','Source Han Sans KR','Malgun Gothic','맑은 고딕', sans-serif;
}

html[lang^='ja'] > body
{
    font-family: Fira Sans, 'Noto Sans JP','Source Han Sans JP','Meiryo UI','Meiryo','メイリオ', sans-serif;
}

input, textarea, select
{
   font-family: 'Fira Sans', sans-serif;
}

a
{
   text-decoration: none;

   &:hover,
   &:active,
   &:focus
   {
      text-decoration: underline;
   }
}

h1
{
   font-size: 3em;
   font-weight: 500;
   line-height: #{(4 / 3)};
}

p
{
   margin: 0 0 1.5em;
}

table
{
   width: 100%;
   border-collapse: collapse;

   th,
   td
   {
      margin: 0;
      border: 0;
      padding: 4px;

      .button
      {
         display: block;
      }
   }

   thead th
   {
      border-bottom: $border-size solid;
      text-align: left;
   }

   &#user-table
   {
      @media(max-width: 980px)
      {
         a[href^='mailto:']
         {
            text-indent: -1000vw;
            display: inline-block;
            position: relative;

            &:after
            {
               content: "\f0e0";
               font-family: 'FontAwesome';
               position: absolute;
               top: 0;
               left: 0;
               text-indent: 0;
            }
         }

         th:nth-child(3),
         td:nth-child(3),
         th:nth-child(4),
         td:nth-child(4),
         th:nth-child(5),
         td:nth-child(5)
         {
            display: none;
         }
      }
   }
}

.at-share-btn-elements
{
   margin-left: -8px;

   > .at_flat_counter
   {
      font-size: 14px !important;
      vertical-align: top !important;
   }

   > .at-share-btn
   {
      margin-left: 8px !important;
   }
}

.trim-wrapper
{
   overflow: hidden;
}

.container
{
   width: $container-width;
   max-width: 88%;
   margin: 0 auto;
   position: relative;
}

.no-wrapping
{
   text-overflow: ellipsis;
   overflow: hidden;
   white-space: nowrap;
}

.eager-wrapping
{
   word-wrap: break-word;
}

.field-validation-error,
.text-danger
{
   color: $strong-red;
}

.text-success
{
   color: $strong-green;
}

.field-validation-error
{
   margin: #{(1em / 3)} 0;
   display: block;
}

a.button,
input[type=submit].button
{
   display: inline-block;
   vertical-align: middle;
   padding: #{(1em / 2)} 1em #{(1em / 3)};
   background-color: $nord10;
   color: #fff;
   text-decoration: none;
   border-radius: $border-size;
   text-align: center;

   &.edit-button
   {
      background-color: $strong-yellow;
   }

   &.delete-button
   {
      background-color: $strong-red;
   }

   &.add-button
   {
      background-color: $strong-green;
   }
}

header#page-header
{
   h1
   {
      font-weight: 300;
      margin: .33em 0;

      a
      {
         text-decoration: none;
      }
   }
}

nav#page-navigation
{
   border-bottom-width: 0;
   border-bottom-style: solid;
   position: relative;
   z-index: 10;

   #page-navigation-links
   {
      margin: 0 -15px #{(1px / 3)}; /* bottom margin fixes Chrome in 4k */
      padding: 0;
      text-align: right;

      > li
      {
         display: inline-block;
         vertical-align: top;

         > a
         {
            display: block;
            padding: 14px 14px 11px;

            &:hover,
            &:active,
            &:focus
            {
               text-decoration: none;
            }
         }
      }
   }

   button
   {
      display: none;
      width: 100%;
      border: 0;
      font-size: 1.2em;
      font-weight: 300;
      padding: #{(2em / 3)} 0;
      background: none;
   }
}

.dropdown-parent
{
   .dropdown-menu
   {
      box-sizing: border-box;
      display: none;
      position: absolute;
      left: -15px;
      right: -15px;
      max-width: #{($container-width + 30px)};
      padding: 15px 15px 0;
      text-align: left;
      z-index: 10;
      border-width: 0;
      flex-wrap: wrap;

      h4
      {
         margin: 0 0 .5em;
         font-size: 1.2em;
         font-weight: 500;
      }

      .dropdown-menu-block
      {
         display: inline-block;
         vertical-align: top;
         margin-bottom: 15px;
         flex-grow: 1;
      }

      .dropdown-menu-block
      {
         ul
         {
            padding: 0;
            display: flex;
            flex-wrap: wrap;
         }

         li
         {
            list-style-type: none;
            line-height: 2em;
            width: 133px;
            vertical-align: top;
            flex-grow: 1;

            > a
            {
               display: block;
               padding: #{(1em / 3)} 1em #{(1em / 6)};

               &:hover,
               &:active,
               &:focus
               {
                  text-decoration: none;
               }
            }
         }
      }

      #settings-theme-menu
      {
         width: 120px;
         flex-grow: 1;
      }

      #settings-lang-menu
      {
         width: 320px;
         flex-grow: 1000;
      }

      #user-action-menu
      {
         ul
         {
            justify-content: flex-start;
            text-align: center;
         }

         .fa
         {
            vertical-align: 1px;
         }
      }
   }

   &.open
   {
      .dropdown-menu
      {
         display: flex;
      }
   }
}

article
{
   padding: 3em 0;

   h1
   {
      font-size: 4em;
      font-weight: 300;
      text-align: center;
      margin: 0 0 #{(3em / 4)};

      &.eager-wrapping
      {
         font-size: 4em;
      }
   }

   h2
   {
      text-align: center;
      font-size: 3em;
      font-weight: bold;
      margin: 1em 0 #{(2em / 3)};
      text-transform: uppercase;
      letter-spacing: #{(1em / 6)};
      border-bottom: $border-size solid;
   }

   h3
   {
      font-size: 2em;
      font-weight: bold;
      margin: 1em 0 #{(2em / 3)};
      text-transform: uppercase;
      letter-spacing: #{(1em / 4)};
      border-bottom: $border-size solid;
   }

   h4
   {
      margin: 1em 0 0;
   }

   ul
   {
      margin-bottom: 1em;

      > li
      {
         margin-bottom: 0.5em;
      }
   }

   .build-group-listing
   {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin: 0 #{(-3em / 2)};

      .build-group
      {
         width: 240px;
         flex-grow: 1;
         margin: 0 #{(3em / 2)} 3em;
         box-sizing: border-box;
         border: $border-size solid $nord10;
         padding: 0.5em 1.5em;

         h3
         {
            background-color: $nord10;
            color: #fff;
            margin: -0.25em -0.75em 0.75em;
            font-size: 2em;
            line-height: 1em;
            font-weight: normal;
            padding: 0;
            letter-spacing: 0;
            border-bottom: 0;
            text-transform: lowercase;
            text-align: center;

            a
            {
               color: #fff;
               text-decoration: none !important;
               display: block;
               padding: #{(1em / 3)} 0;
            }
         }

         p
         {
            font-size: 1.1em;
            font-weight: 300;
            margin: 0 0 #{(2em / 3)};
         }
      }

      .build-group-empty
      {
         width: 270px;
         flex-grow: 1;
         margin: 0 0.75em;
         box-sizing: border-box;
      }
   }
}

.latest-family-wrapper
{
   display: flex;
   flex-wrap: wrap;
   margin-right: -4em;
   overflow: hidden;

   .latest-family
   {
      flex-grow: 1;
      margin-right: 4em;

      > h3
      {
         margin-bottom: 0.5em;
         text-transform: none;
         letter-spacing: 2px;
         font-weight: normal;
      }
   }
}

.latest-flex
{
   display: flex;
   flex-wrap: wrap;
   justify-content: flex-start;
   margin: 0 -2em 0 0;

   .latest-flex-item
   {
      width: 240px;
      box-sizing: border-box;
      flex-grow: 1;
      margin: 0 2em 2em 0;
      border: $border-size solid;

      &:hover,
      &:active,
      &:focus
      {
         text-decoration: none;
      }

      h4.latest-flex-title
      {
         text-transform: uppercase;
         letter-spacing: #{(1em / 4)};
         border-bottom: 0;
         margin: 0;
         padding: 0;
         font-size: 1.25em;
         font-weight: normal;
         text-align: center;
         padding: 0.4em 0.5em;
         margin: -1px;
         color: #fff;
      }

      &.latest-flex-red
      {
         border-color: $strong-red;

         h4.latest-flex-title
         {
            background: $strong-red;
         }
      }

      &.latest-flex-yellow
      {
         border-color: $strong-yellow;

         h4.latest-flex-title
         {
            background: $strong-yellow;
         }
      }

      &.latest-flex-blue
      {
         border-color: $strong-blue;

         h4.latest-flex-title
         {
            background: $strong-blue;
         }
      }

      &.latest-flex-green
      {
         border-color: $strong-green;

         h4.latest-flex-title
         {
            background: $strong-green;
         }
      }

      .latest-flex-detail
      {
         text-align: center;
         font-weight: 300;
         line-height: #{(3 / 2)};

         .latest-flex-build
         {
            font-size: #{(7em / 3)};
            margin: #{(1em / 2)} 0 0;
            font-weight: normal;
            line-height: 1;
         }

         .latest-flex-lab
         {
            font-size: #{(4em / 3)};
            margin: 0 0 0.5em;
         }

         .latest-flex-time
         {
            margin: 0 0 0.75em;
         }
      }
   }
}

.latest-full
{
   display: block;
   background: $nord10;
   font-weight: normal;
   text-align: center;
   color: #fff;
   margin: 0;
   padding: #{(2em / 3)};

   &:hover,
   &:active,
   &:focus
   {
      text-decoration: none;
   }
}

.build-details-flex
{
   display: flex;
   font-size: 1.1em;
   flex-wrap: wrap;

   .build-details-flex-item
   {
      flex-grow: 1;
      margin-bottom: 1.5em;
      width: 180px;

      label
      {
         font-weight: bold;
         display: inline-block;
         vertical-align: top;
         margin-right: 1em;
         min-width: 100px;
      }

      .build-details-flex-value
      {
         display: inline-block;
         vertical-align: top;
         margin-right: 20px;
      }
   }
}

.form-group
{
   margin-bottom: 1.5em;
   display: flex;
   flex-wrap: wrap;

   > label,
   > .label-placeholder
   {
      width: 20%;
      max-width: 280px;
      min-width: 160px;
      text-align: left;
      font-weight: bold;
      margin-right: 1em;
      display: block;
      vertical-align: top;
      margin-top: #{(1em / 4)};
      flex-grow: 1;
      text-align: right;

      @media(max-width: 479px)
      {
         width: 100%;
         max-width: none;
         text-align: left;
      }
   }

   > div
   {
      width: 40%;
      min-width: 240px;
      max-width: 560px;
      display: block;
      vertical-align: top;
      flex-grow: 2;

      input, textarea, select
      {
         width: 100%;
         box-sizing: border-box;
         border: $border-size solid;
         padding: #{(1em / 3)} #{(1em / 2)};
         border-radius: #{(1em / 6)};
         font-size: 1em;
         line-height: 1em;
         border-color: #888;
      }

      .group-input-button
      {
         display: flex;

         input
         {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            border-right: 0;
         }

         button
         {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            width: 120px;
            border: $border-size solid #888;
            border-left: 0;
         }
      }

      input[type=submit],
      input[type=checkbox]
      {
         width: auto;
      }

      > input[type=submit],
      > button,
      .group-input-button > button
      {
         display: inline-block;
         vertical-align: middle;
         padding: #{(1em / 2)} 1em #{(1em / 3)};
         background-color: $strong-green;
         color: #fff;
         text-decoration: none;
         border-radius: #{(1em / 6)};
         border: 0;
         line-height: 1.6;
         font-size: 1em;
      }

      a.button
      {
         vertical-align: middle;
         padding: #{(1em / 2)} 1em #{(1em / 3)};
         line-height: 1.6;
         font-size: 1em;
      }

      &.wide-group
      {
         width: 40%;

         > .trumbowyg-box
         {
            width: 100%;
            margin: 0;
         }
      }
   }

   > label + div
   {
      margin-left: 0;
   }
}

.credits-wrapper
{
   display: flex;
   flex-wrap: wrap;

   .credits-list
   {
      width: 480px;
      max-width: 100%;
      flex-grow: 1;
   }
}

.credits-list
{
   dt
   {
      font-weight: bold;
   }

   dd
   {
      margin-left: 0;
   }

   dd + dt
   {
      margin-top: 1.5em;
   }
}

ul.pagination
{
   text-align: center;
   margin: 0.5em 0 1em;
   padding: 0;
   display: flex;
   justify-content: center;

   > li
   {
      list-style: none;
      width: 1.8em;
      line-height: 1.8em;
      margin: 0 #{(1em / 3)};

      > a,
      > span
      {
         display: block;
         border-radius: #{(1em / 3)};
         text-decoration: none;
         padding-top: 0.15em;
      }

      > span
      {
         cursor: not-allowed;
      }
   }
}

footer#page-footer
{
   padding: #{(4em / 3)} 0 #{(4em / 6)};
   font-size: 0.85em;

   .footer-flex
   {
      display: flex;
      flex-wrap: wrap;

      .footer-flex-item
      {
         width: 50%;
         min-width: 200px;
         text-align: center;
         flex-grow: 1;

         &:last-child
         {
            text-align: right;
         }

         &:first-child
         {
            text-align: left;
         }
      }
   }

   p
   {
      margin: 0 0 #{(2em / 5)};
   }
}

#modal-search-overlay
{
   display: none;
   position: fixed;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   transition: background-color linear 0.6s, -webkit-backdrop-filter linear 0.6s, backdrop-filter linear 0.6s;
   background-color: rgba(0,0,0, 0);
   -webkit-backdrop-filter: blur(0);
   backdrop-filter: blur(0);
   z-index: 100;

   &.open
   {
      display: block;
      background-color: rgba(0,0,0, 0.75);
      -webkit-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);
   }

   #modal-search
   {
      position: absolute;
      top: 15%;
      left: 25%;
      width: 50%;
      max-height: 70%;
      overflow: auto;
      padding: 2em;
      border-radius: 2px;
      box-sizing: border-box;
      border: $border-size solid #ccc;

      h3
      {
         margin: 0 0 1em;
         font-size: 1.5em;
         font-weight: 300;
      }

      > #modal-search-box
      {
         width: 100%;

         > *
         {
            display: inline-block;
            height: 2.5em;
            padding: 0.5em;
            box-sizing: border-box;
            border: $border-size solid;
         }

         > #modal-search-input
         {
            width: calc(100% - #{(10em / 3)});
         }

         > #modal-search-button
         {
            width: #{(8em / 3)};
         }
      }

      > #modal-search-result
      {
         display: flex;
         flex-wrap: wrap;
         justify-content: space-between;

         > .search-result-item
         {
            display: block;
            padding: 0 1em;
            width: 140px;
            flex-grow: 1;

            > .search-result-heading
            {
               font-size: 1.2em;
               margin-bottom: 0;
            }
         }
      }
   }
}

#menu-open-overlay
{
   display: none;
   position: fixed;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   z-index: 5;

   &.open
   {
      display: block;
   }
}

@media (max-width: 980px)
{
   #modal-search-overlay #modal-search
   {
      left: 10%;
      width: 80%;
   }
}

@media (max-width: 640px)
{
   header#page-header h1
   {
      text-align: center;
   }

   nav#page-navigation
   {
      button
      {
         display: block;
      }

      #page-navigation-links
      {
         display: none;
         padding-bottom: 1em;

         &.open
         {
            display: block;
         }

         > li
         {
            display: block;
            text-align: left;
         }
      }
   }

   .dropdown-parent .dropdown-menu
   {
      position: static;

      .dropdown-menu-block
      {
         min-width: 100%;

         li
         {
            min-width: 50%;
            text-align: left;
         }
      }
   }

   article
   {
      h1,
      h1.eager-wrapping
      {
         font-size: 3em;
         line-height: #{(4 / 3)};
      }

      h3
      {
         text-align: center;
      }

      .addthis_sharing_toolbox
      {
         text-align: center;
      }
   }

   footer#page-footer .footer-flex .footer-flex-item
   {
      &:first-child
      {
         text-align: center;
         margin-bottom: #{(3em / 2)};
      }

      &:last-child
      {
         text-align: center;
      }
   }
}

@media (max-width: 1300px)
{
   .latest-flex .latest-flex-item
   {
      min-width: calc(50% - 2em);
   }
}
